import 'babel-polyfill';
import React, {
    Component,
    PropTypes
} from 'react';
import Page from '../../../common/page';
import ReactDOM from 'react-dom';
import MultiList from 'yo3/component/multilist';
import Toast from 'yo3/component/toast/';


class SimplaMultiList extends Component {
    constructor(props) {
        const dataSource = {
            subItemType: 'MENU',
            subList: [{
                name: '1',
                value: 1,
                subItemType: 'CHECKBOX',
                subList: [{
                    name: '1-1 默认选项',
                    value: '1-1'
                }, {
                    name: '1-2',
                    value: '1-2'
                }, {
                    name: '1-3',
                    value: '1-3'
                }, {
                    name: '1-4',
                    value: '1-4'
                }]
            }, {
                subItemType: 'RADIO',
                name: '2',
                value: 2,

                subList: [{
                    name: '2-1',
                    value: '2-1',
                    itemType: 'MENU',
                    subItemType: 'CHECKBOX',
                    subList:[{
                        name: '2-1-1',
                        value: '2-1-1'
                    },{
                        name: '2-1-2',
                        value: '2-1-2'
                    }]
                }, {
                    name: '2-2',
                    value: '2-2'
                }, {
                    name: '2-3',
                    value: '2-3'
                }]
            }, {
                name: '3',
                value: 3,
                subItemType: 'CHECKBOX',
                subList: [{
                    name: '3-1',
                    value: '3-1'
                }, {
                    name: '3-2',
                    value: '3-2'
                }, {
                    name: '3-3',
                    value: '3-3'
                }, {
                    name: '3-4',
                    value: '3-4'
                }]
            }]
        };
        super();
        this.state = {
            dataSource,
            value: [1, ['1-1']]
        }
    }
    updateValue(value) {
        this.setState({
            value,
        })
    }
    render() {
        return (
            <Page
                title="multiList Demo"
                extraClass="demo-content"
            >
                <MultiList
                    dataSource={ this.state.dataSource}
                    value={this.state.value}
                    onChange={({newValue}) => {
                        this.updateValue(newValue);
                        Toast.show(`您的选择是${newValue.join('>')}`)
                    }}
                />
            </Page>
        )
    }
}

ReactDOM.render( <SimplaMultiList /> , document.querySelector('#container'));



